import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSpinner, faExclamationCircle } from '@fortawesome/free-solid-svg-icons';
import './renderHelper.css';

const Icon = ({ icon }) => (
    <FontAwesomeIcon 
        icon = {icon}
        size = "6x" 
    />
)

const SpinningIcon = ({ icon }) => {
    const [speed, setSpeed] = useState(3);
    return (
        <FontAwesomeIcon 
            icon = {icon}
            size = "6x" 
            style = {{animation: `spin ${speed}s linear infinite`}}
        />
    )
}

export const element = (icon, text) => (
    <div 
    className = "warn"
    style = {{
        display : 'flex',
        justifyContent : 'center',
        alignItems : 'center',
        flexDirection : 'column',
        width : '100%',
        height : '100%',
        padding : '10%'}}
    >
    {icon}
    <span
        style = {{
            fontSize : '24px',
            padding : '10%',
        }}
        className = "loading-text"
    >
        {text}
    </span>
</div>
)
export const Loading = () => {
        const text = "Î°úÎî©Ï§ë..."

        return element(<SpinningIcon icon = {faSpinner} />, text);
}

export const Error = ({ error }) => {    
    let msg = error.toString();
    let text = "ÏóêÎü¨ Î∞úÏÉù : " + msg;
    if(msg.includes("404")) text = "404 Î¶¨ÏÜåÏä§Î•º Ï∞æÏßÄ Î™ªÌñàÏäµÎãàÎã§.";
    if(msg.includes("500")) text = "500 ÏÑúÎ≤Ñ Ïò§Î•òÏûÖÎãàÎã§. Îπ†Î•¥Í≤å Ï°∞ÏπòÌïòÍ≤†ÏäµÎãàÎã§ !";

    return element(<Icon icon = {faExclamationCircle} />, text);
}



export const NotFound = () => {

    return element(null , 'Í≤∞Í≥ºÍ∞Ä ÏóÜÏäµÎãàÎã§. üòì');
}

export const NotFound404 = () => {
    return element(<Icon icon = {faExclamationCircle} />, "404 Î¶¨ÏÜåÏä§Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.")
}

export const renderAfterApiCall = (items, error, loading, element) => {

    if(error){
        return <Error error = {error}/>;
    }
    if(loading){
        return <Loading />;
    }
    if(!items){
        return <NotFound />;
    }
    return element;
}


