import React, { useEffect, useState } from 'react';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import { fetchItems, fetchVotes } from '../modules/apiCaller';
import { ContextConsumer } from '../context/ContextContainer';
import Modal from 'react-bootstrap/Modal';
import './Vote.css';
import axios from 'axios';

const VoteCheckModal = ({ show, setShow,  handleVote}) => {

    const handleClose = () => {
        setShow(false);
    }

    return (
        <Modal show = {show}>
            <Modal.Body>
                <p>ÌïúÎ≤à Ìà¨ÌëúÌïòÎ©¥ Ï∑®ÏÜåÌï† Ïàò ÏóÜÏäµÎãàÎã§. <br />
                   Í∑∏ÎûòÎèÑ Ìà¨ÌëúÌïòÏãúÍ≤†Ïñ¥Ïöî?</p>
            </Modal.Body>

            <Modal.Footer>
                <Button variant="secondary" onClick = {handleClose}>Îã´Í∏∞</Button>
                <Button variant="primary" onClick = {handleVote}>Ìà¨Ìëú</Button>
            </Modal.Footer>
        </Modal>
    )
}

const VoteFormElement = ({ type, id, isRendered, value , setValue }) => {
    // mbti Ï°∞Ìöå
    const [mbtis , setMbtis] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error , setError] = useState(null); 

    // mbti Ìà¨Ìëú
    const [selectedMbti, setSelectedMbti] = useState(null);

    const [show, setShow] = useState(false);

    useEffect(() => {
        const url = "/mbti/api/v1/list";
        if(isRendered){
            fetchItems(url, 'mbti', setMbtis, setError, setLoading);
            fetchVotes(type, id, setValue);
        }
    }, [])

    // state Î≥Ñ Î†åÎçîÎßÅ
    const createMbtiOptions = (mbtis) => {
        if(loading || !mbtis) return (<option>Î∂àÎü¨Ïò§Îäî Ï§ë</option>)
        if(error) return (<option>ERROR : {error}</option>)
        return mbtis.map(mbti => {
            let name = mbti['name'];
            return (
                <option key = {mbti.id} value={mbti.id}>
                    {name.toUpperCase()}
                </option>
            )
        })
    }

    // select ÏóêÏÑú mbti ÏÑ†ÌÉùÏãú Ï≤òÎ¶¨
    const handleChange = (e) => setSelectedMbti(e.target.value);

    // Ìà¨ÌëúÌïòÍ∏∞ Î≤ÑÌäºÏ≤òÎ¶¨
    const handleVote = async () => {

        if(!selectedMbti){
            alert("MBTIÎ•º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.")
            return;
        }

        try{
            const url = "/vote/api/v1/mbti/" + selectedMbti + "/" + type + "/" + id;
            await axios.post(url);
            fetchVotes(type, id,  setValue);
            alert("Ìà¨Ìëú ÏÑ±Í≥µ")
            setShow(false);
        } catch(error){
            alert('Error : ' + error);
        }

    }

    return (
        <>
        <div className = "vote-form" >
        <Form>
            <Form.Select 
                className = "vote-form-select"
                onChange = {handleChange}>
                <option>MBTIÎ•º Í≥†Î•¥ÏÑ∏Ïöî.</option>
                {createMbtiOptions(mbtis)}
            </Form.Select>
            { value.available ? 
            <Button 
                variant = "primary" 
                className = "vote-form-button"
                onClick = {() => setShow(true)}>
                Ìà¨ÌëúÌïòÍ∏∞
            </Button> :
            <Button 
                variant = "dark"
                className = "vote-form-button"
                disabled>
                Ïù¥ÎØ∏ Ìà¨ÌëúÌï®
            </Button>  }           
        </Form>
        </div>
        <VoteCheckModal
        show = {show}
        setShow = {setShow}
        handleVote = {handleVote}
    />
    </>
    );
}

const VoteForm = ({ type , id, isRendered}) => (
    <ContextConsumer >
        {
            ({ state, actions}) => (
                <VoteFormElement
                    type = {type}
                    id = {id}
                    isRendered = {isRendered}
                    value = {state.value}
                    setValue=  {actions.setValue}
                />
            )
        }
    </ContextConsumer>
)

export default React.memo(VoteForm);