import React from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';

const SuccessModal = ({ type ,suffix , show, setShow }) => {

    const handleClose = () => {
        setShow(false);
        if(type === 'comment'){
            window.location.reload();
            return;
        }
        window.history.back();
    }

    return (
        <Modal show = {show}>
            <Modal.Body>
                <p>ÏÑ±Í≥µÏ†ÅÏúºÎ°ú {suffix}ÎêòÏóàÏäµÎãàÎã§.</p>
            </Modal.Body>
            <Modal.Footer>
                <Button 
                    variant="primary" 
                    onClick = {handleClose} >
                        Îã´Í∏∞
                </Button>
            </Modal.Footer>
        </Modal>
    )
}

export default SuccessModal;