import React, { useState, useEffect} from 'react';
import Form from 'react-bootstrap/Form';
import { fetchItems } from '../../modules/apiCaller';

const CategoryList = () => {

    const [categories, setCategories] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    
    useEffect( () => {
        const url = "/category/api/v1/list";
        fetchItems(url, 'category', setCategories, setError, setLoading);
    }, []);

    if(error){  
        return <p>ÏóêÎü¨ Î∞úÏÉù</p>
    }
    if(loading){
        return <p>Î°úÎî©Ï§ë..</p>
    }
    if(!categories){
        return <p>ÏïÑÎ¨¥Í≤ÉÎèÑ ÏóÜÏäµÎãàÎã§.</p>
    }
    return (
        <Form.Select name = "categoryId">
            <option>Ïπ¥ÌÖåÍ≥†Î¶¨Î•º ÏÑ†ÌÉùÌïòÏÑ∏Ïöî.</option>
            {categories.map(category => 
            <option key = {category.id} value = {category.id}> {category.name} </option>)}
        </Form.Select>
    )
}

export default CategoryList;